<template>
  <div class="card-help">
    <div v-html="context"></div>
  </div>
</template>

<script>
import Url from '@/http/url'
export default {
  name: 'EcardHelp',
  data() {
    return {
      context: ''
    }
  },
  mounted() {
    let id = process.env.VUE_APP_ENV === 'development' ? '1457' : '1619'
    this.$http.get(Url.GET_PROCOTOL_BAITIAO + '?id=' + id).then(res => {
      let result = res.data;
      this.context = result.data;
    })
  }
}
</script>

<style lang="scss" scoped>
.card-help{
  padding: .32rem;
  font-size: .26rem;
  color: #999;
  line-height: .4rem;
}
</style>
